        <div v-show="!deleteflg" class="switchArea pt-1" >
          <input type="checkbox" v-bind:id=todo.name v-bind:value=todo.name v-bind:checked=todo.kago v-model="buycheckboxs" @change="onChangeEventHandler(i)" >
