<script src="https://cdn.jsdelivr.net/gh/gitbrent/bootstrap4-toggle@3.6.1/js/bootstrap4-toggle.min.js"></script>

<script>
    
    export default{
        data(){
            return{dogs:[],buycheckboxs:[],
                    imgPath: require('@/assets/pizza.png'),
                    plusbutton: require('@/assets/add_one_button_insert_plus_icon_155856.png'),
                    favorite: require('@/assets/627.png'),
                    boughtlist: require('@/assets/1569.png'),
                    history: require('@/assets/9655.png'),
                    deleteflg:false
            }
        },
        created(){
            this.dogs=JSON.parse(window.localStorage.getItem('dogs'))||[];
            //配列分画像を作る
            for (let k = 0; k < this.dogs.length; k++) {
                 this.dogs[k].id = require("@/assets/" + this.dogs[k].fname );
            }
        },
        methods:{
            deleteTodo(i){
                this.dogs.splice(i,1);
                window.localStorage.setItem('dogs',JSON.stringify(this.dogs));
            },
            goPageAdd(){
                this.$router.push('/todos/add');
            },
            goPageFavorite(){
                this.$router.push('/todos/favorite');
            },
            goPageCheckList(){
                this.$router.push('/');
            },
            goPageHistory(){
                this.$router.push('/todos/history');
            },
        buy(){
            // １．現在日時を取得
            let date = new Date()
            let dispDate = date.getMonth() + "/" + date.getDay() + " " + date.getHours() + ":" + date.getMinutes()
            
            // 保存された履歴と買い物リストを取得
            const rirekis=JSON.parse(window.localStorage.getItem('rirekis'))||[];
            this.dogs=JSON.parse(window.localStorage.getItem('dogs'))||[];
            
            // 買い物リストチェックボックスONの商品名配列を回す
            for (let k = 0; k < this.buycheckboxs.length; k++) {
                for (let m = 0; m < this.dogs.length; m++) {
                    // チェックされた商品をチェックリスト内から探す
                    
                    if (this.buycheckboxs[k] == this.dogs[m].name) {
                        // 購入履歴情報を作成
                        rirekis.unshift({id:"999",name:this.buycheckboxs[k],day:dispDate,fname:this.dogs[m].fname});
                        // チェックリスト情報から削除
                        this.dogs.splice(m, 1);
                    }
                }
            }
            // 編集した履歴と買い物リスト（削除済み）を保存
            window.localStorage.setItem('rirekis',JSON.stringify(rirekis));
            window.localStorage.setItem('dogs',JSON.stringify(this.dogs));
            
            this.$router.push('/todos/history');
			},
			clearAll(){
				//全ストレージデータをクリアする
				window.localStorage.removeItem("todos");
				window.localStorage.removeItem("favs");
				window.localStorage.removeItem("dogs");
				window.localStorage.removeItem("rirekis");
				this.$router.push('/todos/favorite');
			}
        }
    }
</script>

<style>
