 /* === ボタンを表示するエリア ============================== */
.switchArea {
  line-height    : 24px;                /* 1行の高さ          */
  letter-spacing : 0;                   /* 文字間             */
  text-align     : center;              /* 文字位置は中央     */
  font-size      : 9px;                /* 文字サイズ         */

  position       : relative;            /* 親要素が基点       */
  margin         : auto;                /* 中央寄せ           */
  width          : 68px;               /* ボタンの横幅       */
