<template>
  <div class="container-fluid">
    <nav class="navbar navbar-expand fixed-top navbar-light mb-5" style="background-color:#e3f2fd;">
      <span class="mx-auto mt-1" style="font-size:16px;">お気に入り</span>
    </nav>
  <ul class="nav nav-tabs mt-5">
    <li class="nav-item">
      <a href="#contents1" class="nav-link active" data-toggle="tab" v-on:click="goPageFavorite">おうちの定番</a>
    </li>
    <li class="nav-item">
      <a href="#contents2" class="nav-link" data-toggle="tab" v-on:click="goPageCheckList">買い物リスト</a>
    </li>
    <li class="nav-item">
      <a href="#contents3" class="nav-link" data-toggle="tab" v-on:click="goPageHistory">買い物履歴</a>
    </li>
  </ul>
    <div class="overflow-auto mt-1" style="height:500px;">
      <div class="form-inline rounded  border border-info mt-2 mb-1" v-for="(cfav,i) in nekos"  v-bind:key="i">
        <div class="text-center col-2"><img v-bind:src="nekosphots[i]" width="20" ></div>
        <div class="text-left col-7">{{cfav.name}}</div>
        <div class="col-3"><toggle-button :labels="{checked: '買う！', unchecked: 'ある'}" 
                                           v-model="toggle"
                                           @change="onChangeEventHandler(i)"
                                          :color="{checked: '#FF0000', unchecked: '#00FF00', disabled: '#CCCCCC'}"
                                           width="60">false</toggle-button></div>
      </div>
    </div>
  </div>
</template>
<script>
    export default{
        data(){
            return{nekos:[],
                   plusbutton: require('@/assets/app_button_plus.png'),
                   favorite: require('@/assets/627.png'),
                   boughtlist: require('@/assets/1569.png'),
                   history: require('@/assets/9655.png')
            } 
        },
        created(){
            this.nekos = JSON.parse(window.localStorage.getItem('favs'))||[];
            
            if (this.nekos.length == 0) {
                this.nekos = [{id:"1",name:"ホルスタイン牛乳",status:false,fname:"milk.png"},
                            {id:"2",name:"愛知県産トマト",status:false,fname:"tomato.png"},
                            {id:"3",name:"山梨産デラウェア",status:false,fname:"grape.png"},
                            {id:"4",name:"ピザ（マルゲリータ）",status:false,fname:"pizza.png"},
                            {id:"5",name:"ピザ（ピザーラ）",status:false,fname:"pizza.png"},
                            {id:"6",name:"ピザ（ドミノ）",status:false,fname:"pizza.png"},
                            {id:"7",name:"シャインマスカット",status:false,fname:"grape.png"},
                            {id:"8",name:"巨峰",status:false,fname:"grape.png"},
                            {id:"9",name:"千葉県産トマト",status:false,fname:"tomato.png"},
                            {id:"10",name:"北海道牛乳",status:false,fname:"milk.png"},
                            {id:"11",name:"小岩井産ミルク",status:false,fname:"milk.png"},
                            ];
            }else{
                // すでにあるので何もしない
            }
            
            // 写真データ用の配列を用意する
            this.nekosphots = [];
            
            //配列分画像を作る
            for (let k = 0; k < this.nekos.length; k++) {
                this.nekosphots[k] = require("@/assets/" + this.nekos[k].fname );
            }
            
            //this.Constfavs=JSON.parse(window.localStorage.getItem('favs'))||[];
        },
        methods:{
            goPageFavorite(){
                this.$router.push('/todos/favorite');
            },
            goPageCheckList(){
                this.$router.push('/');
            },
            goPageHistory(){
                this.$router.push('/todos/history');
            },
            // トグルボタン変更イベント
            onChangeEventHandler: function(i){
                // 買い物チェックリストの中身
                const dogs=JSON.parse(window.localStorage.getItem('dogs'))||[];
                
                const selectSyouhin = this.nekos[i].name
                const selectFileNm = this.nekos[i].fname
                
                
                // トグル＝true（買う）の場合
                if (this.toggle) {
                    // 買い物チェックリスト（todos）の中にすでにお気に入りがいた場合
                    // todosの中からお気に入り商品を消さない
                    let count = 0;
                    for (let j = 0; j < dogs.length; j++) {
                        if (dogs[j].name === selectSyouhin) {
                            count = 1;
                        }
                    }
                    if (count == 0) {
                        // 買うものリストに追加
                        dogs.push({id:"a",name:selectSyouhin,status:false,fname:selectFileNm,kago:false});
                        window.localStorage.setItem('dogs',JSON.stringify(dogs));
                        
                    }
                    
                // トグル＝false（ある）の場合
                }else{
                    // 買い物チェックリスト（todos）の中にすでにお気に入りがいた場合
                    // todosの中からお気に入り商品を消す
                    for (let j = 0; j < dogs.length; j++) {
                        if (dogs[j].name === selectSyouhin) {
                            dogs.splice(j, 1)
                            window.localStorage.setItem('dogs',JSON.stringify(dogs));
                        }
                    }
                }
                // constfavsに変更したステータスを設定
                this.nekos[i].status = this.toggle
                // ステータスの状態を保存する
                window.localStorage.setItem('favs',JSON.stringify(this.nekos));
            }
        }
    }
</script>

