import Vue from 'vue'
import Router from 'vue-router'
import PageCheckList from '../components/CheckList.vue'
import PageAdd from '../components/Add.vue'
import PageFavorite from '../components/Favorite.vue'
import PageHistory from '../components/History.vue'

Vue.use(Router)

export default new Router({
	mode:'history',
	base:process.env.BASE_URL,
	routes:[
		{
			path:'/',
			component:PageCheckList
		},
		{
			path:'/todos/add',
			component:PageAdd
			
		},
		{
			path:'/todos/favorite',
			component:PageFavorite
		},
		{
			path:'/todos/history',
			component:PageHistory
		}
	]
})